
:root {
  font: 17px sans-serif;
}

body {
  margin: 0;
  padding: 0 12.5%;
  min-height: 100vh;
}

main {
  background-color: #FFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid #333;
  border-top: none;
  padding-bottom: 2rem;
}

#controls {
  display: flex;
  gap: 1rem;
}

#controls input {
  display: block;
}

#coordinate-submit {
  display: block;
  width: 100%;
  margin-top: 1rem;
  padding: 1rem;
  font: bold 1rem sans-serif;
  text-transform: uppercase;
}

#query-content {
  margin-top: 1.8rem;
  padding: 1.1rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

#city {
  font: bold 1em/1.5em sans-serif;
  text-transform: uppercase;
}

#city-info {
  font: 1em/1.5em sans-serif;
  text-transform: uppercase;
  color: #0008;
  margin-bottom: 1.4rem;
}

#forecast {
  font: 2.4rem sans-serif;
}

#temperature {
  font: bold 4.3rem sans-serif;
}
